// FLEX HELPERS
.flex {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  @media screen and (min-width: 48em) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  &-xs {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 48em) {
      flex-direction: row;
    }
  }
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-start {
  display: flex;
  justify-content: flex-start;
}

.flex-end {
  display: flex;
  justify-content: flex-end;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.flex-grow {
  flex-grow: 1;
}

// SPACING HELPERS

.w-100 {
  width: 100%;
}

.w-50 {
  width: 100%;

  @media screen and (min-width: 72em) {
    width: 50%;
  }
}

