@import '_colors';
@import '_variables';
@import '_borders';
@import '_fonts';
@import '_links';
@import '_lists';
@import '_forms';
@import '_buttons';
@import '_contact_overlay';
@import '_contact_method';
@import '_grid';
@import '_images';
@import '_helpers';
@import "navbar";


html {
  font-size: 16px;
  min-height: 100vh;
  height: 100%;
}

body {
  font-family: $font-body;
  font-weight: 300;
  background-color: $color-white;
  color: $color-black;
  margin: 0;
  height: 100%;
}

main {
  position: relative;
  top: $height-header-xs;
  min-height: 100%;

  @media screen and (min-width: 48em) {
    top: $height-header-md;
  }
}

footer {
  position: relative;
  bottom: 0;
  top: $height-header-xs;

  @media screen and (min-width: 48em) {
    top: $height-header-md;
  }
}
